* {
  margin: 0;
  padding: 0; }

html, body, #root {
  height: 100%;
  width: 100%; }

.app {
  text-align: center;
  background: black;
  color: white;
  height: 100%; }

.app.timerActive {
  background: #f7f7f7; }

.app-logo {
  height: 80px; }

.app-logo.animate {
  -webkit-animation: App-logo-spin infinite 1s linear;
          animation: App-logo-spin infinite 1s linear; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.app-header {
  background-color: #3b5998; }

.app-header,
.app-footer button {
  color: white; }

.app-header {
  font-size: 1.5em;
  height: 150px;
  padding: 20px; }

.time-container {
  font-size: 15em;
  margin: 0; }

.time-container.down {
  color: #3b5998; }

.time-container.down.ready {
  color: #00d8ff; }

.app.timerActive .time-container,
.app.timerActive .timelist,
.app.timerActive .stats {
  color: black; }

.time {
  font-family: Courier; }

.timelist, .stats {
  font-size: 1.5em; }

.timelist {
  padding: 0 40%; }

.stats {
  list-style: none;
  padding: 0;
  height: 60px;
  width: 100%; }

.stats li {
  float: left;
  width: 25%; }

.stats .stats-label {
  color: #00d8ff; }

.app-footer {
  display: -ms-flexbox;
  display: flex; }

.app-footer .button-container {
  -ms-flex-item-align: center;
      align-self: center;
  border-left: 1px solid black;
  border-right: 1px solid black; }

.app-footer button {
  font-size: .75em;
  background-color: #3b5998;
  border: none;
  height: 100%;
  width: 100%; }

@media only screen and (max-width: 1024px) {
  h2 {
    margin: 0; }
  .app-logo {
    height: 40px; }
  .app-header {
    height: 13%;
    font-size: .8em;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; }
  .app-body {
    height: 87%; }
  .time-container {
    font-size: 7em; }
  * {
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Chrome/Safari/Opera */
    /* Konqueror */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none; } }

@media only screen and (max-width: 667px) {
  .app-header {
    height: 17%;
    padding: 5px 20px; }
  .app-body {
    height: 76%; }
  .app-footer {
    height: 7%; }
  .time-container {
    font-size: 4em; }
  .stats {
    height: 132px; }
  .stats li {
    width: 50%;
    margin-bottom: 10px; }
  .timelist {
    padding: 0 20%; }
  .app-footer .button-container {
    width: 33.33333%;
    height: 100%; } }



